const Generator = require("./generator");
const { LuaScript } = require("./script");
const lodash = require('lodash');

/**
 * Standard generate workflow
 *
 * @param {string} data
 * @param {object} options
 */
function generate(data, options) {
    let defaultOption = { hooks: {} };
    let _option = Object.assign(defaultOption, options);

    // Init schedule map
    const scheduleMap = [];

    // Step.1 Init generator
    const generator = new Generator(_option);

    // Step.2 Parse data to json tree
    const schema = generator.str2json(data);

    // Step.3 Build schedule map
    // Map all groups
    let instructionSet = schema.data
    for (let instruction of instructionSet) {

            // Create a new schedule
            let schedule = {
                handler: 'f' + instruction.uuid.slice(0,5),
                args: new Set(),
                script: new LuaScript(),
                _ref: instruction,

                ...lodash.pickBy(instruction, (value,key) => {
                    return key.startsWith('$')
                }),
            };
            scheduleMap.push(schedule);

            // Extends more scripts build
            if (schema.mode == "nest") {
                generator.nestWorkflowFuncBuilder(schedule, _option.hooks);
            }
    }

    return scheduleMap;
}

module.exports = generate;
