const { isValidData } = require("./utils");
const { format } = require("lua-json");
const { pinyin } = require("pinyin-pro");
const md5 = require("md5");
const { map } = require("lodash");
// const { sterilizer } = require("sterilizer");

/**
 * Script Template
 *
 * @class Script
 */
class Script {
    constructor() {
        this.code = "";
    }

    /**
     * Insert a line to the code
     *
     * @param {string} code
     * @param {string} str
     * @param {boolean} [newLine=true]
     */
    insertLine(str, newLine = true) {
        this.code += str;
        if (newLine) {
            this.code += "\n";
        }
        return this;
    }

    /**
     * Insert an indentation to the code
     *
     * @param {*} str
     * @param {number} [space=1]
     * @memberof Script
     */
    insertSubLine(str, space = 1) {
        this.code += " ".repeat(space) + str;
        this.code += "\n";
        return this;
    }

    /**
     * Return the code
     *
     * @return {string} code
     * @memberof Script
     */
    getCode() {
        return this.code;
    }
}

/**
 * Lua Script Template
 *
 * @class LuaScript
 * @extends {Script}
 */
class LuaScript extends Script {
    constructor(config) {
        super();

        // meta
        this.lang = "lua";
        this.version = "1.0.0";

        // constants
        this.ENV = "tEnv";
    }

    /**
     * Create function declaration
     *
     * @param {object} [params={}]
     * @param {function} next
     * @memberof LuaScript
     */
    buildFuncSkeleton(schedule, next, hook) {
        // Build function declaration
        let name = schedule.handler;
        this.insertLine(`function ${name}(${this.ENV})`);

        // Do hook
        hook && hook(schedule);

        // Build function body
        next && next(schedule);

        // Build function end
        this.insertLine(`end`);

        return this;
    }

    /**
     * Build Function name
     *
     * @param {*} args
     * @memberof LuaScript
     */
    buildFunctionName(...args) {
        let name = args.map((arg) => this.buildValidKeyName(arg)).join("__");
        return "f" + md5(name).slice(0, 6);
    }

    /**
     * Build English Key Name
     * ex. function name
     *
     * @param {*} str
     * @memberof LuaScript
     */
    buildValidKeyName(str) {
        let isValidKey = /\w/.test(str);
        let name = str;
        if (!isValidKey) {
            let pyArr = pinyin(str, { toneType: "none", type: "array" });

            name = pyArr.map((py) => py.toUpperCase().split("").shift() + py.slice(1)).join("");

            // name = sterilizer(name).safe().toString()
        }

        return name;
    }

    /**
     * Build if statement
     *
     * @param {object} branch
     * @param {function} hook
     * @return {*}
     * @memberof LuaScript
     */
    buildIfStatement(branch, next, hooks) {
        if (isValidData(branch.value)) {
            // Build if statement params
            let condition = this.buildExpression(branch.field, branch.value);

            // Build if statement
            this.insertSubLine(`if(${condition})then`);

            // System Arg Register hook
            hooks.__registerArgs(branch.field);

            // Do hook
            let hook = hooks[branch.field];
            hook && hook(branch);

            // Build if statement body
            next && next(branch);

            // Build if statement end
            this.insertSubLine(`end`);
        } else {
            // Ignore empty condition
            next && next();
        }
    }

    /**
     * Build Nested If Statement
     *
     * @param {object} currentNode is a node with children in an array
     * @param {object} codeSnippet is the code snippet instance
     * @memberof LuaScript
     */
    buildNestStatement(currentNode, hooks = {}) {
        if (currentNode.children?.length) {
            this.buildIfStatement(
                currentNode,
                () => {
                    for (let branch of currentNode.children) {
                        this.buildNestStatement(branch, hooks);
                    }
                },
                hooks
            );
        } else if (currentNode.return) {
            this.buildIfStatement(currentNode, () => {
                this.buildReturnStatement(currentNode.return);
            },hooks);
        }
    }

    /**
     * Build return statement
     *
     * @param {*} value
     * @memberof LuaScript
     */
    buildReturnStatement(values) {
        // FIXME:需要有映射指定，以确定return返回值的类型
        map(values,(val,key) => {
            values[key] = Number(val);
        })
        this.insertSubLine(format(values));
    }

    /**
     * Build condition
     * ex. params for `if` statement
     *
     * @param {string} type
     * @param {string|number|boolean} value
     * @param {object|undefined} options
     * @memberof LuaScript
     */
    buildExpression(field, value) {
        let arg = this.buildExpressionArgument(field);
        let exp = this.buildEntireExpression(arg, value);

        return `${exp}`;
    }

    /**
     * Build expression left
     * Always read env value from first argument(table,match the field)
     *
     * @param {string} field
     * @memberof LuaScript
     */
    buildExpressionArgument(field) {
        return `${this.ENV}["${field}"]`;
    }

    /**
     * Build expression operator
     *
     * @param {string} value
     * @memberof LuaScript
     */
    buildEntireExpression(arg, value) {
        return this.buildRangeExpression(arg, value) || this.buildCompareExpression(arg, value);
    }

    /**
     * match compare expression
     * WARNING: 支持用户填写=或无符号
     *
     * @param {*} value
     * @return {*}
     * @memberof LuaScript
     */
    buildCompareExpression(arg, value) {
        const compareRe = /([><=~]?=?)(.*)/;
        let compare = compareRe.exec(value);
        if (compare) {
            // =x 或 x 返回 ==x
            let operator = !compare[1] || compare[1] == "=" ? "==" : compare[1];
            // 布尔值转换
            let _value = this.transformValue(compare[2]);
            return `${arg} ${operator} ${_value}`;
        }
    }

    /**
     * pure string value
     *
     * @param {string} value
     * @return {*}
     * @memberof LuaScript
     */
    transformValue(value) {
        if (isNaN(Number(value))) {
            switch (value) {
                case "是":
                    return "true";
                case "否":
                    return "false";
                default:
                    return `"${value}"`;
            }
        } else {
            return Number(value);
        }
    }

    /**
     * match range expression
     * WARNING: 支持2个负数之间的区间，当为2个负数时，建议表达为-1~-2
     *
     * @param {*} value
     * @memberof LuaScript
     */
    buildRangeExpression(arg, value) {
        const rangeRe = /(-?\d+)[-~](-?\d+)/;
        let range = rangeRe.exec(value);
        if (range) {
            let start = range[1];
            let end = range[2];

            return `${arg} >= ${start} and ${arg} <= ${end}`;
        }
    }
}

module.exports = { Script, LuaScript };
