const Parser = require("./parser");

/**
 * Parse the workbook to formatted json object
 *
 * @param {BinaryData} data
 * @return {json}
 * @memberof Parser
 */
async function parse(data, options = {}) {
    return new Promise((resolve, reject) => {
        try {
            // Step.1 Init parser
            const parser = new Parser(options);

            // Step 2: Load the workbook
            let { worksheet } = parser.load(data, options._xlsx);

            // Step.3 Init headers
            parser.init(worksheet,options.keymap);

            // Step 4: Extract the worksheet
            let workjson = parser.extract(worksheet);

            // Step 5: Build the json data tree
            parser.build(workjson, options.mode);

            resolve(parser);
        } catch (e) {
            reject(e);
        }
    });
}

module.exports = parse;
