const XLSX = require("xlsx");
const Utils = require("./utils");
const ParserError = require("./error");
const { pick, pickBy, isPlainObject, last, map } = require("lodash");

// Parser Definition
class Parser {
    // Inspect the workbook
    constructor(options) {
        // Settings
        this._options = options;
        this._MAX_CONDITION_KEYS = Math.min(options._MAX_CONDITION_KEYS || 26);

        // the headers
        this.headers = []; //表格原列名
        this.fields = []; //标准化列名
        this.handlers = []; //控制器字段
        this.returns = []; //返回字段
        this.conditions = []; //条件字段
        this.keymap = options.keymap;
        this.order = options.order || null;

        // th data
        this._xlsx = options._xlsx || { type: "buffer" };
        this._workbook = null;
        this._worksheet = null;
        this._workjson = null;
        this.mode = options.mode || "nest";
        this.data = [];
    }

    /**
     * Get the workbook
     *
     * xlsx is a popular library for reading xlsx files
     * documentation: https://docs.sheetjs.com/docs/
     *
     * @param {Binary} data
     * @return {json} xlsx object
     * @memberof Parser
     */
    load(rawData, xlsxOptions) {
        const workbook = XLSX.read(rawData, xlsxOptions);
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        this._workbook = workbook;
        this._worksheet = worksheet;
        return { workbook, worksheet };
    }

    /**
     * Init
     *
     * when the column key starts with __, it is not a condition column
     * if will not to add to the conditional branches
     *
     * @memberof Parser
     */
    init(worksheet, keymap) {
        let _keymap = this.autoRenameKeymap(worksheet, keymap);

        this.headers = Object.keys(_keymap);
        this.fields = Object.values(_keymap);

        this.fields.forEach((key) => {
            switch (key.slice(0, 1)) {
                case "$":
                    this.handlers.push(key);
                    break;
                case "_":
                    this.returns.push(key);
                    break;
                default:
                    this.conditions.push(key);
            }
        });
    }

    /**
     * Auto rename keymap
     * if keymap is Chinese, transfer to alphabet
     * if keymap is English, continue
     * if keymap has no handlers and returns
     * specify the first as handler,the last one as return
     *
     * @memberof Parser
     */
    autoRenameKeymap(worksheet, keymap) {

        let _keymap = {}

        // 用户自定义
        if (isPlainObject(keymap)) {
            let values = Object.values(keymap);

            // 检查长度
            this.checkConditionLength(values);

            // 检查重名
            this.checkDuplicateName(values);

            // 检查格式
            this.checkIsValidName(values);

            // 必须包含至少1个句柄
            this.checkHasHandle(values);

            _keymap = keymap

        // 自动创建keymap
        } else {

            let _originNames = this.getHeaderRow(worksheet);

            const startASCII = 96  //97a~122z
            map(_originNames,(item,i) => {
                if(i == 0){
                    _keymap[item] = '$h'
                }else if(i == _originNames.length - 1){
                    _keymap[item] = '_r'
                }else{
                    _keymap[item] = String.fromCharCode(startASCII + i)
                }
            })

        }

        this.keymap = _keymap;
        return _keymap
    }

    /**
     * Get origin headers
     *
     * @param {*} sheet
     * @return {*}
     * @memberof Parser
     */
    getHeaderRow(sheet) {
        var headers = [];
        var range = XLSX.utils.decode_range(sheet["!ref"]);
        var C,
            R = range.s.r; /* start in the first row */
        /* walk every column in the range */
        for (C = range.s.c; C <= range.e.c; ++C) {
            var cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })]; /* find the cell in the first row */

            var hdr = "UNKNOWN " + C; // <-- replace with your desired default
            if (cell && cell.t) hdr = XLSX.utils.format_cell(cell);

            headers.push(hdr);
        }
        return headers;
    }

    /**
     * Check Duplicate Name
     *
     * @param {*} values
     * @memberof Parser
     */
    checkDuplicateName(values){
        let arrLen = values.length
        let setLen = Array.from(new Set(values)).length
        if( arrLen != setLen ) {
            throw new ParserError({
                e: new Error(),
                msg: `There is some duplicate name in keymap`,
            });
        }
    }

    /**
     * 检查keymap条件项长度
     *
     * @param {*} values
     * @memberof Parser
     */
    checkConditionLength(values) {
        let conditionKeys = values.filter((item) => {
            return !item.startsWith("$") && !item.startsWith("_");
        });

        if (conditionKeys.length > this._MAX_CONDITION_KEYS) {
            throw new ParserError({
                e: new Error(),
                msg: `keymap is too long,keep the length less than 26`,
            });
        }else if(conditionKeys.length < 2){
            throw new ParserError({
                e: new Error(),
                msg: `keymap is too short,keep the length more than 2`,
            });
        }
    }

    /**
     * 满足首字符为_/$/字母
     * 其它额外跟随1个字母/数字
     *
     * @param {*} values
     * @memberof Parser
     */
    checkIsValidName(values) {
        for (let item of values) {
            let isValid = /^[_$a-zA-Z][a-zA-z0-9]*/.test(item);
            if (!isValid) {
                throw new ParserError({
                    e: new Error(),
                    msg: `${item} is not a valid name as argument`,
                });
            }
        }
    }

    /**
     * 检查至少包含一个句柄
     * 是否强制包含返回或多个在此定义
     *
     * @param {*} values
     * @memberof Parser
     */
    checkHasHandle(values) {
        let result = values.filter((item) => {
            return item.startsWith("$");
        });
        if (!result) {
            throw new ParserError({
                e: new Error(),
                msg: `A handle (a key startsWith $) is required`,
            });
        }
    }

    /**
     * Get the worksheet
     *
     * Get the first worksheet
     * Most workbooks have only one worksheet, but Excel has multiple sheets
     *
     * @param {json} workbook
     * @return {json} json object array
     * @memberof Parser
     */
    extract(worksheet) {
        let sheetOption = {
            blankrows: false,
            defval: null,
        };

        sheetOption.header = this.fields;

        const workjson = XLSX.utils.sheet_to_json(worksheet, sheetOption);

        // when use keymap & ignore the first row
        workjson.shift();

        this._workjson = workjson;
        return workjson;
    }

    /**
     * Build the json data tree
     *
     * you can specify the headers to customize the condition order to improve the performance
     * example: ["temperature", "wind", "..."]
     *
     * @param {json} workjson object array
     * @return {json} json object
     * @memberof Parser
     */
    build(workjson, mode = "nest") {
        let data = [];

        if (mode === "nest") {
            this.buildNestData(workjson, data);
        } else {
            this.buildFlowData(workjson, data);
        }

        this.data = data;
        return data;
    }

    /**
     * Json List
     *
     * @param {array} workjson
     * @param {array} dataList
     * @memberof Parser
     */
    buildFlowData(workjson, data) {
        // Map the data
        for (let row of workjson) {
            // TODO:flat
        }
    }

    /**
     * Nest json object array
     *
     * @param {array} workjson
     * @param {array} dataTree
     * @memberof Parser
     */
    buildNestData(workjson, data) {
        // Map the data
        for (let row of workjson) {
            // 指定组
            let $keys = pick(row, this.handlers);
            let uuid = Utils.uuid([...Object.values($keys)]);
            let i = Utils.findMatchedRow(data, "uuid", uuid);

            if (i < 0) {
                data.push(
                    Object.assign(
                        {
                            uuid,
                            branches: [],
                        },
                        $keys
                    )
                );
                i = data.length - 1;
            }

            // Reset & Record the current node
            let currentNode = data[i]["branches"];
            this.__mergeRow(currentNode, row);
        }
    }

    /**
     * Deal with single row
     *
     * @param {array} currentNode
     * @param {object} row
     * @param {object} options
     * @memberof Parser
     */
    __mergeRow(currentNode, row) {
        // Specify the conditions
        const conditions = this.order || this.conditions;

        // Map the row fields,insert a child node
        for (let field of conditions) {
            // Check if the current node list has the same value node
            let matchedIndex = Utils.isExistCondition(currentNode, field, row[field]);

            if (matchedIndex < 0) {
                // If the condition is not exist
                // insert a child node
                this.__createNode(currentNode, field, row);
                currentNode = currentNode[currentNode.length - 1]["children"];
            } else {
                // If the condition is exist
                // go forward next child node
                currentNode = currentNode[matchedIndex]["children"];
            }
        }
    }

    /**
     * Create a new node
     *
     * @param {array} currentNode
     * @param {string} field
     * @param {string} data
     * @memberof Parser
     */
    __createNode(currentNode, field, row) {
        let newNode = {
            // KEY
            field, //列名

            // VALUE
            value: row[field], //当前条件的值
            // md5: Utils.getMd5(row[field]), //存在复杂数据值时，可以用md5来标识和比较
        };

        // Set the special field
        let isLastCondition = this.__isTheLastKey(field);
        if (isLastCondition) {
            newNode["return"] = {};
            this.returns.forEach((key) => {
                newNode["return"][key] = row[key];
            });
        } else {
            newNode["children"] = [];
        }

        currentNode.push(newNode);
    }

    /**
     * Check the field is the last condition
     *
     * @param {*} key
     * @memberof Parser
     */
    __isTheLastKey(key) {
        return [...this.conditions].pop() === key;
    }

    /**
     * Get the property of the instance object
     *
     * @param {string} [key='data']
     * @return {*}
     * @memberof Parser
     */
    get(key = "data") {
        return this[key];
    }

    /**
     * Output the instance
     *
     * @memberof Parser
     */
    toJSON() {
        let project = pickBy(this, function (value, key) {
            return !key.startsWith("_");
        });

        try {
            return JSON.stringify(project, null, 4);
        } catch (e) {
            throw new ParserError({
                e: e,
                msg: "Project is not a valid json object.",
                data: project,
            });
        }
    }
}

module.exports = Parser;
