const md5 = require("md5");

/**
 * Find is there an item has the same key-value pair in the array
 * TODO:当存在需要比较的复杂数据时，可以用md5来标识和比较
 *
 * @param {*} arr
 * @param {*} key
 * @param {*} val
 * @return {*}
 */
function isExistCondition(arr, field, data) {
    if (arr.length) {
        for (let [i, item] of arr.entries()) {
            if (item["field"] === field && item["value"] === data) {
                return i;
            }
        }
    }
    return -1;
}

/**
 * 找到匹配数组条目（数组元素为一个Object，其指定field与value一致）
 *
 * @param {*} arr
 * @param {*} field
 * @param {*} value
 * @return {*}
 */
function findMatchedRow(arr, field, value) {
    if (arr.length) {
        for (let [i, item] of arr.entries()) {
            if (item[field] == value) return i;
        }
    }
    return -1;
}

/**
 * 根据多个值生成一个uuid
 *
 * @param {*} values
 */
function uuid(...values) {
    return md5([...values].join("_"));
}

/**
 * Get the filed value md5 for complex data
 * when the value is invalid value ,return ''
 *
 * @param {*} val
 * @return {*}
 */
function getMd5(val) {
    if (val !== undefined && val !== null && val !== "") {
        return md5(val);
    }
    return "";
}


module.exports = { isExistCondition, getMd5, findMatchedRow, uuid };
