const { GeneratorError } = require("./error");
const { format } = require("lua-json");
const { cloneDeep,isPlainObject,map } = require("lodash");
const luamin = require("luamin");

/**
 * Generate Scripts from JSON Tree
 * @param {data} string
 * @param {object} options
 *
 * @class Generator
 */
class Generator {
    constructor(config) {
        // origin data json
        this.data = null;
    }

    /**
     * Parse data to json tree
     *
     * @param {*} data
     * @memberof Generator
     */
    str2json(data) {
        try {
            this.data = JSON.parse(data);
        } catch (e) {
            throw new GeneratorError({
                data,
                e,
                msg: "Invalid JSON format",
            });
        }
        return this.data;
    }

    /**
     * Generate workflow function
     * @param {object} schedule
     * @param {object} hooks
     *
     * @memberof Generator
     */
    nestWorkflowFuncBuilder(schedule, hooks) {
        // Build Skeleton
        schedule.script.buildFuncSkeleton(
            schedule,
            () => {
                if (schedule._ref.branches?.length) {
                    for(let branch of schedule._ref.branches) {
                        // Build Nest Statement
                        schedule.script.buildNestStatement(
                            branch,
                            Object.assign(
                                {
                                    __registerArgs: function (arg_name) {
                                        // if (!arg_name.startsWith("$")) {
                                        schedule.args.add(arg_name);
                                        // }
                                    },
                                },
                                hooks
                            )
                        );
                    }
                }
            },
            hooks.__initFunc
        );
    }

    /**
     * Output schedule map to json file
     *
     * @param {array} scriptsMap
     * @param {string} json
     * @memberof Generator
     */
    static inspect(scheduleMap) {
        let _map = cloneDeep(scheduleMap);
        for (let schedule of _map) {
            schedule["args"] = Array.from(schedule.args);
        }
        return JSON.stringify(_map, null, 4);
    }

    /**
     * Output functions code
     *
     * @param {array} object
     * @param {string}
     * @memberof Generator
     */
    static outputFunctions(scheduleMap) {
        let _map = cloneDeep(scheduleMap);
        let scripts = "";
        for (let item of _map) {
            scripts += item.script.code;
            scripts += "\n";
        }
        return scripts;
    }

    /**
     * Output function map
     *
     * @param {object} object
     * @param {string}
     * @memberof Generator
     */
    static outputScheduleMap(scheduleMap) {
        let _map = cloneDeep(scheduleMap);
        for (let item of _map) {
            item["args"] = Array.from(item.args);
            delete item["_ref"];
            delete item["script"];
        }
        return format(_map, {
            spaces: 4,
        });
    }

    /**
     * Output comment
     *
     * @static
     * @memberof Generator
     */
    static outputComment(o){
        let comment = "";
        if(isPlainObject(o)){
            map(o,(val,key) => {
                comment += `\t${key}: ${val}\n`;
            })
        }else{
            comment = o + '\n'
        }
        return `--[[\n${comment}]]\n`
    }

    /**
     * Output code
     *
     * @static
     * @param {array} scheduleMap
     * @return {string}
     * @memberof Generator
     */
    static outputCode(scheduleMap, minify = false) {
        let mapCode = this.outputScheduleMap(scheduleMap);
        let funcCode = this.outputFunctions(scheduleMap);
        let code = funcCode + mapCode;
        if (minify) {
            code = this.minify(code);
        }
        return code;
    }

    /**
     * Minify code
     *
     * @static
     * @param {string} str
     * @return {string}
     * @memberof Generator
     */
    static minify(str) {
        let min = luamin.minify(str);
        return min.replace(/\["(\w+)"\]/g,'.$1')
    }


}

module.exports = Generator;
